@charset "UTF-8";
@import "../../../assets/theme/default/var.css";

@component-namespace template{
  @component form {
    width:100%;
    margin-top:20px;
    @descendent bar{
      margin-top:20px;
    }
  }
}
.grid-form-center{
    text-align: center;
}
.grid-form .el-form-item__label {
 /*   width:80px;*/
}
.grid-form-sm .el-form-item__label {
    width:80px;
}
.grid-form.grid-form-md .el-form-item__label {
    width:100px;
}
.grid-form.grid-form-lg .el-form-item__label {
    width:120px;
}
.grid-form  .grid-form-inline .el-form-item__content{
    width:200px;
}
.grid-form-md .grid-form-inline .el-form-item__content{
    width:200px;
}
.grid-form-lg .grid-form-inline .el-form-item__content{
    width:200px;
}
.grid-form .el-date-editor.el-input{
  width:100%;
}
.input-md{
    width:290px;
}

/* 包含在一个新的标签内定义 */
.grid-form-inline{
  & .el-form-item {
    display: inline-block;
    margin-right: 10px;
    vertical-align: top;
  }
  & .el-form-item__label {
    float: none;
    display: inline-block;
  }
  & .el-form-item__content {
    display: inline-block;
    vertical-align: top;
  }
}
.grid-form-block{
  & .el-form-item {
      display: block;
      margin-right: 10px;
      vertical-align: top;
    }
  & .el-form-item__label {
      float: left;
      display: inline-block;
    }
  & .el-form-item__content {
      display: inline-block;
    min-width:200px;
      vertical-align: top;
    }
}
/* /包含在一个新的标签内定义 */

/* 类名定义在item上 */
.el-form-item.item-inline {
      display: inline-block;
      margin-right: 10px;
      vertical-align: top;
  }
.el-form-item.item-inline .el-form-item__label {
      float: none;
      display: inline-block;
  }
.el-form-item.item-inline .el-form-item__content {
      display: inline-block;
      vertical-align: top;
  }
.el-form-item.item-block .el-form-item {
   /*   display: block;
      margin-right: 10px;
      vertical-align: top;*/
  }
.el-form-item.item-block .el-form-item__label {
   /*   float: left;
      display: inline-block;*/
  }
.el-form-item.item-block .el-form-item__content {
   /* margin-left:80px;*/
min-width:200px;display: inline-block;
     vertical-align: top;
  }
.el-form-item.item-block .el-form-item__content .el-input{
    width:200px;
}
.grid-form  .el-form-item.item-inline .el-form-item__content{
    width:200px;
}
.grid-form-sm  .el-form-item.item-inline .el-form-item__content{
    width:200px;
}
.grid-form-md .el-form-item.item-inline.el-form-item__content{
    width:200px;
}
.grid-form-lg .el-form-item.item-inline .el-form-item__content{
    width:200px;
}


